.toolbar select.toolbar-item {
  border: none;
  border-radius: 8px;
  background-color: white;
  color: #555;
  font-size: 14px;
  padding: 0 32px 0 10px;
  margin: 0 4px;
  cursor: pointer;
  transition: background-color 0.2s ease;
  height: 36px;
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
  line-height: 36px;
