}

.toolbar select.toolbar-item:focus {
  outline: none;
  box-shadow: 0 0 0 2px rgba(0, 120, 212, 0.2);
}

/* Remove default arrow in some browsers */
.toolbar select.toolbar-item::-ms-expand {
  display: none;
