/* Link Dialog Styles */
.link-dialog-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.link-dialog {
  background: white;
  border-radius: 8px;
  padding: 20px;
  min-width: 400px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.link-dialog-field {
  margin-bottom: 16px;
}

.link-dialog-field label {
  display: block;
  margin-bottom: 4px;
  font-weight: 500;
  color: #333;
}

.link-dialog-field input {
  width: 100%;
  padding: 8px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
