  margin-top: 20px;
}

.link-dialog-buttons button {
  padding: 8px 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background: white;
  cursor: pointer;
  font-size: 14px;
  transition: all 0.15s ease;
}

.link-dialog-buttons button:hover {
  background: #f5f5f5;
}

.link-dialog-buttons button[type="submit"] {
  background: #007bff;
  color: white;
  border-color: #007bff;
}

.link-dialog-buttons button[type="submit"]:hover:not(:disabled) {
  background: #0056b3;
  border-color: #0056b3;
}

.link-dialog-buttons button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.link-dialog-buttons button:disabled:hover {
  background: white;
}

